<template>
  <i class="iconfont icon-icon-6 fl" @click="refresh" :class="{refreshAni: isRefresh}" ref="refreshBtn"></i>
</template>

<script>
  import '../assets/css/animation.css'
  export default {
    data() {
      return {
        isRefresh: false
      };
    },
    methods: {
      // 刷新 
      refresh() {
        this.isRefresh = true;
      }
    },
    mounted() {
      const vm = this;
      this.$refs.refreshBtn.addEventListener("animationend", function() { //动画重复运动时的事件
        vm.isRefresh = false;
        vm.$emit('refreshData');
      }, false);
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .icon-icon-6  {
    color: #1caf9a;
    font-size: 24px;
    margin-right: 20px;
    cursor: pointer;
  }
  .refreshAni {
    display: block;
    animation: refresh 1s;
  }
</style>



