import Vue from 'vue'
import Router from 'vue-router'
import {httpi} from './http'
import Main from '@/pages/Main'
import error from '@/pages/Error'
import Device from '@/pages/Device'
import DiskLocation from '@/pages/DiskLocation'
import DiskManage from '@/pages/DiskManage'
import DiskRunConfig from '@/pages/DiskRunConfig'
import FileChangeHistory from '@/pages/FileChangeHistory'
import RemoteOperationHistory from '@/pages/RemoteOperationHistory'
import RouterTransit from '@/pages/routerTransit'
import Null from '@/pages/Null'

Vue.use(Router)

const router = new Router({
  // html5history模式，返回真实路径需要后台配置
  // mode: 'history',
  routes: [{
      path: '/main',
      name: 'Main',
      component: Main,
      children: [{
          path: 'device',
          name: 'Device',
          component: Device
        },
        {
          path: 'disk-location',
          name: 'DiskLocation',
          component: DiskLocation
        },
        {
          path: 'disk-manage',
          name: 'DiskManage',
          component: DiskManage
        },
        {
          path: 'disk-run-config',
          name: 'DiskRunConfig',
          component: DiskRunConfig
        },
        {
          path: 'file-change-history',
          name: 'FileChangeHistory',
          component: FileChangeHistory
        },
        {
          path: 'remote-operation-history',
          name: 'RemoteOperationHistory',
          component: RemoteOperationHistory
        },
        {
          path: 'router-transit',
          name: 'RouterTransit',
          component: RouterTransit
        },
        {
          path: 'null',
          name: 'Null',
          component: Null
        }
        
      ]
    },
    {
      path: '/error',
      name: 'Error',
      component: error
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.name == 'Error') {
    next();
  } else {
    httpi.get('/wyfx/function/verifyFunction', {
        params: {
          path: to.path
        }
      })
      .then(function (response) {
        if (response.data.code == '200') {
          next();
        } else if (response.data.code == '103') {
          httpi.get('/wyfx/page/redirect?path=sc')
            .then(function (response) {
              window.location.href = response.data.msg;
            });
        } else {
          next({
            name: 'Error'
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        next({
          name: 'Error'
        });
      });
  }
})

export default router



