<template>
  <div class="outer">
    <el-row :gutter="10" class="fullHeight">
      <el-col :span="10" class="fullHeight">
        <div class="panel">
          <div class="panel-title">
            {{language.panelTitle1}}
          </div>
          <div class="panel-content">
            <el-table :data="tmTableData" class="table" style="width: 100%;" height="100%">
              <el-table-column prop="onlineTime" :label="language.startTime">
              </el-table-column>
              <el-table-column prop="offlineTime" :label="language.endTime">
              </el-table-column>
            </el-table>
            <div class="pagination">
              <el-pagination layout="sizes, prev, pager, next, total" :current-page="tmCurrentPage" :page-size="tmPageSize" :page-sizes="[20, 50, 100, 200, 400]" :total="tmTotalElements" @current-change="tmCurrentChange" @size-change="tmSizeChange"></el-pagination>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="14" class="fullHeight">
        <div class="panel">
          <div class="panel-title">
            {{language.panelTitle2}}
          </div>
          <div class="panel-content">
            <div class="panel-toolBar">
              <span class="panel-toolBar-cs">
                  {{language.returnMethod}}
                  <span style="color: #1caf9a">{{returnMethod}}</span>
              </span>
              <span class="panel-toolBar-cs">
                  {{language.dataPicker}}
                  <el-date-picker @change="dataPickerChange" v-model="dateRangeVal" type="daterange" :placeholder="language.DPph">
                  </el-date-picker>
                </span>
            </div>
            <el-table :data="reTableData" class="recordTable" style="width: 100%;" height="100%">
              <el-table-column :label="language.modifyWay" width="100">
                <template scope="scope">
                    <span v-if="scope.row.type == 1">{{language.add}}</span>
                    <span v-if="scope.row.type == 2">{{language.modify}}</span>
                    <span v-if="scope.row.type == 3">{{language.del}}</span>
</template>
              </el-table-column>
              <el-table-column prop="path" :label="language.path">
              </el-table-column>
              <el-table-column prop="time" :label="language.modifyTime">
              </el-table-column>
            </el-table>
            <div class="pagination">
              <el-pagination layout="sizes, prev, pager, next, total" :current-page="reCurrentPage" :page-size="rePageSize" :page-sizes="[20, 50, 100, 200, 400]"
                :total="reTotalElements" @current-change="reCurrentChange" @size-change="reSizeChange"></el-pagination>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        language: {},
        tmTableData: [],
        tmTotalElements: 0,
        tmCurrentPage: 1,
        tmPageSize: 20,
        reTableData: [],
        reTotalElements: 0,
        reCurrentPage: 1,
        rePageSize: 20,
        returnMethod: '',
        dateRangeVal: ''
      }
    },
    methods: {
      // 获取开始时间
      getStartTime() {
        if (this.dateRangeVal[0] != '' && this.dateRangeVal[0] != null && this.dateRangeVal[0] != undefined) {
          return this.$formatDate(this.dateRangeVal[0], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      // 获取结束时间
      getEndTime() {
        if (this.dateRangeVal[1] != '' && this.dateRangeVal[1] != null && this.dateRangeVal[1] != undefined) {
          return this.$formatDate(this.dateRangeVal[1], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      // 获取使用时间表格数据
      getTmTableData(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/device/getOnlineAndOfflineTime', {
            params: {
              pageNo: pageNo,
              pageSize: vm.tmPageSize,
              deviceId: device.id
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.renderTmTable(response);
            }
          });
      },
      // 渲染使用时间表格
      renderTmTable(response) {
        this.tmTableData = response.data.data.content;
        this.tmTotalElements = response.data.data.totalElements;
        this.tmCurrentPage = response.data.data.pageNo;
      },
      tmSizeChange(pageSize) {
        this.tmPageSize = pageSize;
        this.getTmTableData(1);
      },
      tmCurrentChange(currentPage) {
        this.getTmTableData(currentPage);
      },
      // 获取操作记录表格数据
      getReTableData(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/device/getOperationLog', {
            params: {
              deviceId: device.id,
              pageNo: pageNo,
              pageSize: vm.rePageSize,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime()
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.renderReTable(response);
            }
          });
      },
      // 渲染使用时间表格
      renderReTable(response) {
        this.returnMethod = response.data.map.signalType;
        this.reTableData = response.data.data.content;
        this.reTotalElements = response.data.data.totalElements;
        this.reCurrentPage = response.data.data.pageNo;
      },
      reSizeChange(pageSize) {
        this.rePageSize = pageSize;
        this.getReTableData(1);
      },
      reCurrentChange(currentPage) {
        this.getReTableData(currentPage);
      },
      // 日期选择改变
      dataPickerChange() {
        this.reCurrentPage = 1;
        this.getReTableData(1);
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      let device = this.$parent.options[this.$parent.optionsVal];
      vm.$http.all([
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'FileChangeHistory'
            }
          }),
          vm.$http.get('/wyfx/device/getOnlineAndOfflineTime', {
            params: {
              deviceId: device.id,
              pageNo: 1,
              pageSize: vm.tmPageSize,
            }
          }),
          vm.$http.get('/wyfx/device/getOperationLog', {
            params: {
              deviceId: device.id,
              pageNo: 1,
              pageSize: vm.rePageSize,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime()
            }
          })
        ])
        .then(vm.$http.spread(function(language, tmData, reData) {
          // 獲取文本信息
          vm.language = language.data.data;
          // 使用时间
          if (tmData.data.code == 200) {
            vm.renderTmTable(tmData);
          }
          // 操作记录
          if (reData.data.code == 200) {
            vm.renderReTable(reData);
          }
          loading.close();
        }))
        .catch(error => {
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>
<style scoped>
  .table {
    width: 100%;
    height: calc(100% - 42px);
  }
  .recordTable {
    width: 100%;
    height: calc(100% - 82px);
  }
  .panel-toolBar {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  .panel-toolBar-cs {
    margin: 0 10px;
  }
</style>



