<template>
  <div class="outer">
    <!-- 修改设备信息弹窗 -->
    <el-dialog :title="language.deviceInfoDialogTitle" size="tiny" :visible.sync="dialogVisible" :close-on-click-modal="false" @open="dialogOpen">
      <el-form label-width="100px" :model="dialogFrom">
        <el-form-item :label="language.deviceName">
          <el-input v-model="dialogFrom.name"></el-input>
        </el-form-item>
        <el-form-item :label="language.deviceCapacity">
          <el-input v-model="dialogFrom.capacity"></el-input>
        </el-form-item>
        <el-form-item :label="language.deviceRemark">
          <el-input type="textarea" :rows="4" v-model="dialogFrom.remark"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">{{language.cancel}}</el-button>
          <el-button type="primary" @click="handleEditDevice">{{language.submit}}</el-button>
        </span>
    </el-dialog>
    <!-- 修改设备信息弹窗end -->
    <el-row :gutter="10" class="fullHeight">
      <el-col :span="11" class="fullHeight">
        <div class="panel">
          <div class="panel-title">
            {{language.deviceInfo}}
            <el-button type="primary" size="small" class="panel-title-btn" @click="dialogVisible = true">{{language.deviceInfoBtn}}</el-button>
          </div>
          <div class="panel-content">
            <el-row type="flex" justify="space-around" align="middle" class="fullHeight">
              <el-col :span="8">
                <img :src="require('../assets/img/device.png')" alt="" style="width: 100%">
              </el-col>
              <el-col :span="12">
                <div class="clearfix deviceInfo">
                  <div class="deviceInfo-title fl">
                    {{language.deviceName}}
                  </div>
                  <div class="deviceInfo-txt fl">
                    {{deviceInfo[cardIndex].name}}
                  </div>
                </div>
                <div class="clearfix deviceInfo">
                  <div class="deviceInfo-title fl">
                    {{language.deviceCapacity}}
                  </div>
                  <div class="deviceInfo-txt fl">
                    {{deviceInfo[cardIndex].count}}
                  </div>
                </div>
                <div class="clearfix deviceInfo">
                  <div class="deviceInfo-title fl">
                    {{language.deviceRemark}}
                  </div>
                  <div class="deviceInfo-txt fl">
                    {{deviceInfo[cardIndex].note}}
                  </div>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-col>
      <el-col :span="13" class="fullHeight">
        <div class="panel">
          <div class="panel-title">
            <el-button-group class="panel-title-btnGroup" size="small">
              <el-button type="primary" size="small" :class="{active: btnGroupActive[0]}" @click="btnGroupClick(0)">{{language.btnGroupAll}}</el-button>
              <el-button type="primary" size="small" :class="{active: btnGroupActive[1]}" @click="btnGroupClick(1)">{{language.btnGroupOL}}</el-button>
              <el-button type="primary" size="small" :class="{active: btnGroupActive[2]}" @click="btnGroupClick(2)">{{language.btnGroupOFL}}</el-button>
            </el-button-group>
          </div>
          <div class="panel-content clearfix" :class="{hide: isNull}">
            <div class="fl card" v-for="(item, index) in deviceInfo" :key="item.id" :class="{active:index == cardIndex}" @click="handlecardClick(index)">
              <div class="card-status clearfix" v-if="item.status == 0">
                <img :src="require('../assets/img/ol.png')" alt="" class="fl">
                <span class="fl">{{language.olTxt}}</span>
              </div>
              <div class="card-status clearfix" v-else>
                <img :src="require('../assets/img/ofl.png')" alt="" class="fl">
                <span class="fl">{{language.oflTxt}}</span>
              </div>
              <el-row type="flex" justify="space-around" align="middle" class="fullHeight">
                <el-col :span="8">
                  <img :src="require('../assets/img/device.png')" alt="" style="width: 100%">
                </el-col>
                <el-col :span="12">
                  <div class="card-content">
                    {{item.name}}
                  </div>
                </el-col>
              </el-row>
            </div>
          </div>
          <div class="nullTxt" :class="{hide: !isNull}">{{language.nullTxt}}</div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
        dialogVisible: false,
        dialogFrom: { // 修改设备信息表单对象
          name: '',
          capacity: '',
          remark: ''
        },
        btnGroupActive: [true, false, false], //设备状态切换按钮active
        deviceInfo: [{
          name: ''
        }], //设备数据对象
        cardIndex: '0', // 设备列表active索引,
        isNull: true, //设备列表是否为空
      }
    },
    methods: {
      // 设备信息修改弹窗开启回调
      dialogOpen() {
        this.dialogFrom = {
          name: this.deviceInfo[this.cardIndex].name,
          capacity: this.deviceInfo[this.cardIndex].count,
          remark: this.deviceInfo[this.cardIndex].note
        }
      },
      // 修改设备信息
      handleEditDevice() {
        this.dialogVisible = false;
        const vm = this;
        this.$httpi.get('/wyfx/device/updateDevice', {
            params: {
              name: this.dialogFrom.name,
              count: this.dialogFrom.capacity,
              note: this.dialogFrom.remark,
              id: this.deviceInfo[this.cardIndex].id
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.$message({
                showClose: true,
                message: vm.language.modifySuc,
                type: 'success'
              });
              vm.deviceInfo[vm.cardIndex].name = vm.dialogFrom.name;
              vm.deviceInfo[vm.cardIndex].count = vm.dialogFrom.capacity;
              vm.deviceInfo[vm.cardIndex].note = vm.dialogFrom.remark;
              vm.$parent.options[vm.cardIndex].name = vm.dialogFrom.name;
              vm.$parent.options[vm.cardIndex].count = vm.dialogFrom.capacity;
              vm.$parent.options[vm.cardIndex].note = vm.dialogFrom.remark;
            } else {
              vm.$message({
                showClose: true,
                message: vm.language.modifyErr,
                type: 'error'
              });
            }
          });
      },
      // 设备状态切换
      btnGroupClick(index) {
        const vm = this;
        let status;
        if (index == 0) {
          status = 2;
        } else if (index == 1) {
          status = 0;
        } else {
          status = -1;
        }
        this.$httpi.get('/wyfx/device/getAllDeviceByStatus', {
            params: {
              status: status
            }
          })
          .then(function(device) {
            if (device.data.code == 200) {
              vm.deviceInfo = device.data.data;
              vm.isNull = false;
            } else {
              vm.deviceInfo = [{
                name: ''
              }];
              vm.isNull = true;
            }
            vm.cardIndex = 0;
            vm.btnGroupActive = [false, false, false];
            vm.btnGroupActive[index] = true;
          });
      },
      // 设备切换
      handlecardClick(index) {
        this.cardIndex = index;
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'Device'
            }
          }),
          vm.$http.get('/wyfx/device/getAllDeviceByStatus', {
            params: {
              status: '2'
            }
          }),
        ])
        .then(vm.$http.spread(function(language, device) {
            // 獲取文本信息
            vm.language = language.data.data;
            // 获取产品列表
            if (device.data.code == 200) {
              vm.deviceInfo = device.data.data;
              vm.isNull = false;
            } else {
              vm.isNull = true;
            }
            loading.close();
          }))
          .catch(error => {
            console.log(error);
            vm.$router.push({
              name: 'Error'
            })
          });
    }
  }
</script>

<style scoped>
  .deviceInfo {
    margin-bottom: 35px;
  }
  .deviceInfo-title {
    display: inline-block;
    width: 80px;
    text-align: right;
    color: #1caf9a;
  }
  .deviceInfo-txt {
    display: inline-block;
    width: calc(100% - 80px);
    line-height: 21px;
    word-break: break-all;
    word-wrap: break-word;
  }
  .card {
    width: 27%;
    height: auto;
    margin: 2%;
    padding: 1%;
    cursor: pointer;
    border: 1px solid #fff;
    border-radius: 5px;
  }
  .card:hover {
    border: 1px solid #1caf9a;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }
  .card.active {
    border: 1px solid #1caf9a;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }
  .card-status {
    margin-bottom: 10px;
  }
  .card-status span {
    font-size: 14px;
  }
  .card-status img {
    margin-top: 3px;
    margin-right: 5px;
  }
  .card-content {
    width: 100%;
    word-break: break-all;
    word-wrap: break-word;
  }
</style>



