<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    name: 'app',
    mounted() {
      this.$router.push({
        name: 'Device'
      });
    }
  }

</script>

<style>
  /*複用樣式*/

  .clearfix:after {
    content: "\200B";
    display: block;
    height: 0;
    clear: both;
  }

  .fl {
    float: left;
  }

  .fr {
    float: right;
  }

  #app {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  }

  * {
    margin: 0;
    padding: 0;
  }

  html,
  body,
  #app {
    height: 100%;
  }

  .fullHeight {
    height: 100%;
  }

  .hide {
    display: none;
  }

  .outer {
    margin: 10px;
    height: calc(100% - 20px);
  }

  .panel {
    height: 100%;
    width: 100%;
    border-radius: 3px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
    background-color: #fff;
  }

  .panel-title {
    text-align: center;
    width: 100%;
    height: 45px;
    line-height: 45px;
    background-color: #1caf9a;
    color: #fcfcfc;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    position: relative;
  }

  .panel-content {
    height: calc(100% - 45px);
  }

  .panel-title-btn {
    position: absolute;
    right: 20px;
    top: 8px;
    background-color: #b9e5df;
    color: #1caf9a;
  }

  .panel-title-btnGroup button {
    border: 1px solid #fff;
  }

  .panel-title-btnGroup button:hover,
  .panel-title-btnGroup button:focus {
    border: 1px solid #fff;
  }

  .panel-title-btnGroup button.active {
    color: #1caf9a;
    background-color: #fff;
  }

  .toolBar {
    height: 55px;
    line-height: 55px;
    padding: 0 20px;
    width: calc(100% - 40px);
    border-radius: 3px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
    background-color: #fff;
  }

  .nullTxt {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    color: #aeb3b7;
    text-align: center;
  }

  .pagination {
    margin-top: 5px;
    text-align: center;
  }
  /*去除百度地图版权*/

  .anchorBL {
    display: none;
  }
  /* elementUI 覆盖 */
  .el-table th {
    background-color: #fff;
  }

  .el-table__footer-wrapper thead div,
  .el-table__header-wrapper thead div {
    background-color: #fff;
    color: #1caf9a;
  }

</style>



