<template>
  <div class="main">
    <el-dialog :title="language.uploadDialogTitle" :visible.sync="uploadDialogVisible" size="tiny">
      <el-upload ref="upload" :action="this.$rootPath + '/wyfx/sdFile/uploadFile'" :data="upLoadData" :file-list="fileList" multiple>
        <el-button size="small" slot="trigger" type="primary">{{language.uploadDialogBtn1}}</el-button>
        <el-button size="small" @click="clearUploaded">{{language.uploadDialogBtn2}}</el-button>
        <div slot="tip" class="el-upload__tip">{{language.uploadDialogTip}}</div>
      </el-upload>
    </el-dialog>
    <div class="left">
      <div class="logo">
        <i>Stone Fish</i>
      </div>
      <el-row style="margin-top: 25px">
        <el-col :span="24">
          <el-menu :default-active="$route.path" router :class="{hide: isNull}">
            <el-menu-item class="left-nav" :class="{hide:authority[0].ishide}" index="/main/device"><i class="iconfont nav-icon icon-icon-5"></i>{{language.nav_Device}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[1].ishide}" index="/main/disk-manage"><i class="iconfont nav-icon icon-icon-1"></i>{{language.nav_DiskManage}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[2].ishide}" index="/main/file-change-history"><i class="iconfont nav-icon icon-icon-"></i>{{language.nav_FileChangeHistory}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[3].ishide}" index="/main/remote-operation-history"><i class="iconfont nav-icon icon-icon-4"></i>{{language.nav_RemoteOperationHistory}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[4].ishide}" index="/main/disk-location"><i class="iconfont nav-icon icon-icon-2"></i>{{language.nav_DiskLocation}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[5].ishide}" index="/main/disk-run-config"><i class="iconfont nav-icon icon-icon-3"></i>{{language.nav_DiskRunConfig}}</el-menu-item>
          </el-menu>
        </el-col>
      </el-row>
    </div>
    <div class="right">
      <el-row class="right-menu">
        <el-col :span="24" class="clearfix">
          <div class="fl">
            <el-select v-model="optionsVal" @change="handleOptionChange">
              <el-option v-for="(item,index) in options" :key="item.id" :label="item.name" :value="index">
              </el-option>
            </el-select>
          </div>
          <div class="fr">
            <el-button class="right-set" @click="handleTurn('sc')">
              <i class="iconfont icon-icon-11" style="margin-right:5px"></i>{{language.top_sc}}
            </el-button>
            <el-button class="right-set">
              <i class="iconfont icon-icon-12" style="margin-right:5px"></i>{{userName}}
            </el-button>
          </div>
        </el-col>
      </el-row>
      <div class="container">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
        userName: '',
        authority: [{
            code: 'Device',
            ishide: true
          },
          {
            code: 'DiskManage',
            ishide: true
          },
          {
            code: 'FileChangeHistory',
            ishide: true
          },
          {
            code: 'RemoteOperationHistory',
            ishide: true
          },
          {
            code: 'DiskLocation',
            ishide: true
          },
          {
            code: 'DiskRunConfig',
            ishide: true
          }
        ],
        options: [],
        optionsVal: 0,
        isNull: false,
        uploadDialogVisible: false,
        fileList: [],
        upLoadData: {}
      }
    },
    methods: {
      // 跳轉到其他系統
      handleTurn(url) {
        this.$httpi.get('/wyfx/page/redirect?path=' + url)
          .then(function(response) {
            window.location.href = response.data.msg;
          });
      },
      // 设备改变
      handleOptionChange(index) {
        this.$router.replace({
          name: 'RouterTransit',
          params: {
            name: this.$route.name
          }
        });
      },
      // 清空上传列表
      clearUploaded() {
        this.$refs.upload.clearFiles();
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([
          vm.$http.get('/wyfx/user/getLoginUser'),
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'Main'
            }
          }),
          vm.$http.get('/wyfx/function/getMenuBySystem', {
            params: {
              code: 'SF'
            }
          }),
          vm.$http.get('/wyfx/device/getAllDeviceByStatus', {
            params: {
              status: '2'
            }
          })
        ])
        .then(vm.$http.spread(function(user, language, menu, device) {
          //获取用户信息
          vm.userName = user.data.msg;
          // 獲取文本信息
          vm.language = language.data.data;
          // 獲取菜单权限
          let data = menu.data.data;
          for (let i in data) {
            for (let j in vm.authority) {
              if (vm.authority[j].code == data[i].code) {
                vm.authority[j].ishide = false;
              }
            }
          }
          // 获取设备列表
          if (device.data.code == 200) {
            vm.options = device.data.data;
            vm.isNull = false;
          } else {
            vm.isNull = true;
            vm.$router.push({
              name: 'Null'
            });
          }
          loading.close();
        }))
        .catch(error => {
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>

<style scoped>
  .main {
    height: 100%;
  }
  .left {
    width: 230px;
    height: 100%;
    background-color: #292d30;
    color: #d9e1e5;
  }
  .right {
    margin-left: 230px;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
  }
  .logo {
    width: 100%;
    height: 60px;
    line-height: 60px;
    background-color: #23272b;
    font-size: 24px;
    text-align: center;
  }
  .left-nav {
    background-color: #292d30;
    color: #d9e1e5;
    height: 46px;
    line-height: 46px;
  }
  .left-nav:hover,
  .left-nav.is-active {
    background-color: #1caf9a;
    color: #fff;
  }
  .right-menu {
    width: 100%;
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
    border-bottom: 1px solid #d7dadd;
    background-color: #fff;
  }
  .iconfont {
    margin-right: 10px;
  }
  .el-dropdown-menu {
    border-color: #a5b2bf;
  }
  .right-set {
    border: none;
    background-color: #fff;
    color: #565c61;
  }
  .right-set:hover {
    color: #1caf9a;
  }
  .container {
    width: 100%;
    position: absolute;
    top: 60px;
    bottom: 0;
    background-color: #e4e7ea;
  }
  .nav-icon {
    font-size: 24px;
    color: #d9e1e5;
  }
  .hide {
    display: none;
  }
</style>

<style>
  /*重写element  */
  .el-submenu__title {
    background-color: #292d30;
    height: 45px;
    line-height: 45px;
    color: #d9e1e5;
  }
  .el-submenu__title:hover {
    background-color: #1caf9a;
    color: #fff;
  }
  .el-submenu__icon-arrow {
    color: #d9e1e5
  }
  .el-menu {
    border-radius: 0;
  }
</style>



