<template>
  <div class="outer">
    <div class="panel">
      <el-row type="flex" class="fullHeight" align="middle" justify="space-between">
        <el-col :span="24">
          <div class="txt">{{language.txt}}</div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          txt: '您一个设备都没有噢！:('
        }
      }
    },
    mounted() {
      const vm = this;
      vm.$httpi.get('/wyfx/page/getPageMsg', {
          params: {
            msg: 'Null'
          }
        })
        .then(function(response) {
          vm.language = response.data.data;
        })
    }
  }
</script>
<style scoped>
  .txt {
    color: #777f8f;
    font-size: 24px;
    text-align: center;
  }
</style>




