<template>
  <div class="outer">
    <div class="panel" id="bMap">
    </div>
    <div class="bMap-list">
      <el-collapse v-model="collapseActive" accordion>
        <el-collapse-item name="1">
          <template slot="title">
              {{language.collapseTitle}}
              <el-button type="text" class="collapse-btn" :class="{hide: btnHide}" @click.stop="collBtnClick">{{language.collapseBtn}}</el-button>
</template>
          <el-table :data="tableData" class="table" @row-click="tableChange" highlight-current-row>
            <el-table-column prop="date" :label="language.tabTime">
            </el-table-column>
            <el-table-column :label="language.tabLL">
<template scope="scope">
  <span>{{scope.row.latitude}} , {{scope.row.longitude}}</span>
</template>
            </el-table-column>
          </el-table>
          <div class="pagination">
            <el-pagination layout="prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :total="totalElements"
              @current-change="handleCurrentChange"></el-pagination>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>
<script>
  import BMap from 'BMap'
  export default {
    data() {
      return {
        language: {
          tabTime: '获取时间',
          tabLL: '经纬度',
          collapseTitle: '硬盘位置记录',
          collapseBtn: '全部'
        },
        tableData: [],
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 10, // 每页显示条目数
        collapseActive: '0',
        btnHide: true,
        map: '', // 地图对象
        markers: [], // 标注对象
        curMarker: '' // 当前点击的marker
      }
    },
    methods: {
      // 创建地图
      initMap() {
        const vm = this;
        vm.map = new BMap.Map("bMap"); // 创建地图实例  
        var point = new BMap.Point(116.404, 39.915); // 创建点坐标  
        vm.map.centerAndZoom(point, 12); // 初始化地图，设置中心点坐标和地图级别  
        vm.map.enableScrollWheelZoom(true); // 缩放
        // 根据ip定位
        function myFun(result) {
          var cityName = result.name;
          vm.map.setCenter(cityName);
        }
        var myCity = new BMap.LocalCity();
        myCity.get(myFun);
      },
      // 绘制地图
      renderMap(gps) {
        for (var i = 0; i < gps.data.data.length; i++) {
          var point = new BMap.Point(gps.data.data[i].longitude, gps.data.data[i].latitude);
          this.addMarker(point);
        }
      },
      // 添加标注
      addMarker(point) {
        var marker = new BMap.Marker(point);
        this.markers.push(marker);
        this.map.addOverlay(marker);
        marker.addEventListener("click", this.attribute);
      },
      // 标注点击事件
      attribute(e) {
        this.curMarker = e;
        this.collapseActive = '1';
        this.markerRenderTable(1);
      },
      markerRenderTable(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/device/getGpsByLongitudeAndLatitude', {
            params: {
              deviceId: device.id,
              pageNo: pageNo,
              pageSize: vm.pageSize,
              longitude: vm.curMarker.target.point.lng,
              latitude: vm.curMarker.target.point.lat
            }
          })
          .then(function(response) {
            vm.renderTable(response);
            vm.btnHide = false;
            for (let i in vm.markers) {
              vm.markers[i].setAnimation();
            }
            vm.curMarker.target.setAnimation(BMAP_ANIMATION_BOUNCE);
          });
      },
      // 获取gps列表
      getTableData(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/device/getGps', {
            params: {
              deviceId: device.id,
              pageNo: pageNo,
              pageSize: vm.pageSize
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.renderTable(response);
              vm.btnHide = true;
              for (let i in vm.markers) {
                vm.markers[i].setAnimation();
              }
            } else {
              vm.tableData = [];
            }
          });
      },
      // 渲染gps列表
      renderTable(response) {
        this.tableData = response.data.data.content;
        this.totalElements = response.data.data.totalElements;
        this.currentPage = response.data.data.pageNo;
      },
      handleCurrentChange(currentPage) {
        if (this.btnHide == true) {
          this.getTableData(currentPage);
        } else {
          this.markerRenderTable(currentPage);
        }
      },
      // gps列表选择
      tableChange(row) {
        if (row == null) {
          for (let i in this.markers) {
            this.markers[i].setAnimation();
          }
        } else {
          for (let i in this.markers) {
            if (this.markers[i].point.lng == row.longitude && this.markers[i].point.lat == row.latitude) {
              this.map.centerAndZoom(this.markers[i].point, 15);
              this.markers[i].setAnimation(BMAP_ANIMATION_BOUNCE);
            } else {
              this.markers[i].setAnimation();
            }
          }
        }
      },
      // 返回全部gps列表
      collBtnClick() {
        this.getTableData(1);
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.initMap();
      let device = this.$parent.options[this.$parent.optionsVal];
      vm.$http.all([
            vm.$http.get('/wyfx/page/getPageMsg', {
              params: {
                msg: 'DiskLocation'
              }
            }),
          vm.$http.get('/wyfx/device/getGps', {
            params: {
              deviceId: device.id,
              pageNo: 1,
              pageSize: vm.pageSize
            }
          }),
          vm.$http.get('/wyfx/device/getGpsByDistinct', {
            params: {
              deviceId: device.id
            }
          })
        ])
        .then(vm.$http.spread(function(language, list, gps) {
          // 獲取文本信息
            vm.language = language.data.data;
          // 绘制gps列表
          if (list.data.code == 200) {
            vm.renderTable(list);
          }
          // 绘制map
          if (gps.data.code == 200) {
            vm.renderMap(gps);
          }
          loading.close();
        }))
        .catch(error => {
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>

<style scoped>
  .outer {
    position: relative;
  }
  .bMap-list {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 420px;
    max-height: calc(100% - 20px);
    background-color: #fff;
    box-shadow: 1px 2px 1px rgba(0, 0, 0, .15);
    overflow: auto;
  }
  .collapse-btn {
    margin-left: 250px;
  }
  .hide {
    display: none;
  }
</style>



