<template>
  <div class="outer">
    <el-row class="fullHeight">
      <el-col :span="24" class="fullHeight">
        <div class="panel">
          <div class="panel-title">
            <el-button-group class="panel-title-btnGroup" size="small">
              <el-button type="primary" size="small" :class="{active: btnGroupActive[0]}" @click="btnGroupClick(0)">{{language.panelTitle1}}</el-button>
              <el-button type="primary" size="small" :class="{active: btnGroupActive[1]}" @click="btnGroupClick(1)">{{language.panelTitle2}}</el-button>
            </el-button-group>
          </div>
          <div class="panel-content" :class="{hide: panelIshide}">
            <div class="panel-toolBar">
              <span class="panel-toolBar-cs">
                    {{language.returnMethod}}
                    <span style="color: #1caf9a">{{returnMethod}}</span>
              </span>
              <span class="panel-toolBar-cs">
                    {{language.dataPicker}}
                    <el-date-picker @change="dataPickerChange" v-model="dateRangeVal" type="daterange" :placeholder="language.DPph">
                    </el-date-picker>
                  </span>
            </div>
            <el-table :data="reTableData" class="table" style="width: 100%;" height="100%">
              <el-table-column prop="pDate" :label="language.tabPDate"></el-table-column>
              <el-table-column prop="eDate" :label="language.tabEDate"></el-table-column>
              <el-table-column prop="path" :label="language.tabPath"></el-table-column>
              <el-table-column prop="filename" :label="language.tabFileName"></el-table-column>
              <el-table-column :label="language.tabDownCommand">
                <template scope="scope">
                      <span v-if="scope.row.downcommand == 110">{{language.upload}}</span>
                      <span v-if="scope.row.downcommand == 101">{{language.download}}</span>
                      <span v-if="scope.row.downcommand == 111">{{language.del}}</span>
</template>
              </el-table-column>
              <el-table-column :label="language.tabRunType">
<template scope="scope">
  <span v-if="scope.row.runType == 0">{{language.toBeExecuted}}</span>
  <span v-if="scope.row.runType == 1">{{language.completed}}</span>
</template>
              </el-table-column>
              <el-table-column :label="language.tabDataType">
<template scope="scope">
  <span v-if="scope.row.dataType == 0">{{language.toBeExecuted}}</span>
  <span v-if="scope.row.dataType == 1">{{language.completed}}</span>
</template>
              </el-table-column>
              <el-table-column :label="language.tabStatus">
<template scope="scope">
  <span v-if="scope.row.dataType == 0">{{language.executing}}</span>
  <span v-if="scope.row.dataType == 1">{{language.completed}}</span>
</template>
              </el-table-column>
              <el-table-column :label="language.tabAct">
<template scope="scope">
  <el-button size="small" type="text" v-if="scope.row.dataType == 1 && scope.row.downcommand == 101" @click="downLoad(scope.row)">
    {{language.tabDownload}}</el-button>
</template>
              </el-table-column>
            </el-table>
            <div class="pagination">
              <el-pagination layout="sizes, prev, pager, next, total" :current-page="reCurrentPage" :page-size="rePageSize" :page-sizes="[20, 50, 100, 200, 400]"
                :total="reTotalElements" @current-change="reCurrentChange" @size-change="reSizeChange"></el-pagination>
            </div>
          </div>
          <div class="panel-content" :class="{hide: !panelIshide}">
            <el-table :data="tiTableData" class="tiTable" style="width: 100%;" height="100%">
              <el-table-column prop="onlineTime" :label="language.tabOlTime"></el-table-column>
              <el-table-column prop="offlineTime" :label="language.tabOffTime"></el-table-column>
              <el-table-column :label="language.tabUserTime">
<template scope="scope">
   {{scope.row.onlineTime}} ~
  <label style="color: #99a19f">{{scope.row.controllTime}}</label>
</template>
              </el-table-column>
              <el-table-column :label="language.tabDeviceTime">
<template scope="scope">
   {{scope.row.controllTime}} ~
  <label style="color: #99a19f">{{scope.row.offlineTime}}</label>
</template>
              </el-table-column>
              <el-table-column :label="language.tabDetail" width="80">
<template scope="scope">
  <el-button size="small" type="primary" @click="goDetail(scope.row)">
    {{language.tabDetail}}</el-button>
</template>
              </el-table-column>
            </el-table>
            <div class="pagination">
              <el-pagination layout="sizes, prev, pager, next, total" :current-page="tiCurrentPage" :page-size="tiPageSize" :page-sizes="[20, 50, 100, 200, 400]"
                :total="tiTotalElements" @current-change="tiCurrentChange" @size-change="tiSizeChange"></el-pagination>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
        btnGroupActive: [true, false], //设备状态切换按钮active
        returnMethod: '',
        reTableData: [],
        reTotalElements: 0,
        reCurrentPage: 1,
        rePageSize: 20,
        dateRangeVal: '',
        panelIshide: false,
        tiTableData: [],
        tiTotalElements: 0,
        tiCurrentPage: 1,
        tiPageSize: 20,
      }
    },
    methods: {
      // 获取开始时间
      getStartTime() {
        if (this.dateRangeVal[0] != '' && this.dateRangeVal[0] != null && this.dateRangeVal[0] != undefined) {
          return this.$formatDate(this.dateRangeVal[0], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      // 获取结束时间
      getEndTime() {
        if (this.dateRangeVal[1] != '' && this.dateRangeVal[1] != null && this.dateRangeVal[1] != undefined) {
          return this.$formatDate(this.dateRangeVal[1], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      // 面板切换
      btnGroupClick(val) {
        if (val == 0) {
          this.panelIshide = false;
          this.btnGroupActive = [true, false];
        } else {
          this.panelIshide = true;
          this.btnGroupActive = [false, true];
        }
      },
      // 获取操作记录表格数据
      getReTableData(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/sdFile/getParameterLog', {
            params: {
              deviceId: device.id,
              pageNo: pageNo,
              pageSize: vm.rePageSize,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime()
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.renderReTable(response);
            }
          });
      },
      // 渲染操作记录表格
      renderReTable(response) {
        this.returnMethod = response.data.map.signalType;
        this.reTableData = response.data.data.content;
        this.reTotalElements = response.data.data.totalElements;
        this.reCurrentPage = response.data.data.pageNo;
      },
      reSizeChange(pageSize) {
        this.rePageSize = pageSize;
        this.getReTableData(1);
      },
      reCurrentChange(currentPage) {
        this.getReTableData(currentPage);
      },
      // 获取使用时间表格数据
      getTiTableData(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/device/getOnlineAndOfflineTime', {
            params: {
              deviceId: device.id,
              pageNo: pageNo,
              pageSize: vm.tiPageSize
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.renderTiTable(response);
            }
          });
      },
      // 渲染使用时间表格
      renderTiTable(response) {
        this.tiTableData = response.data.data.content;
        this.tiTotalElements = response.data.data.totalElements;
        this.tiCurrentPage = response.data.data.pageNo;
      },
      tiSizeChange(pageSize) {
        this.rePageSize = pageSize;
        this.getTiTableData(1);
      },
      tiCurrentChange(currentPage) {
        this.getTiTableData(currentPage);
      },
      // 日期选择改变
      dataPickerChange() {
        if (this.dateRangeVal == undefined) {
          this.dateRangeVal = [null, null];
        }
        this.reCurrentPage = 1;
        this.getReTableData(1);
      },
      // 下载文件
      downLoad(obj) {
        const vm = this;
        this.$httpi.get('/wyfx/sdFile/isExistFile', {
            params: {
              deviceId: obj.deviceId,
              path: obj.path,
              filename: obj.filename
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              window.open(vm.$rootPath + '/wyfx/sdFile/download?deviceId=' + obj.deviceId + '&path=' + obj.path +
                "&filename=" + obj.filename);
            } else {
              vm.$message({
                showClose: true,
                message: vm.language.files404,
                type: 'error'
              });
            }
          });
      },
      // 跳转详情
      goDetail(obj) {
        let startDate = obj.onlineTime.split(" ");
        startDate = startDate[0];
        startDate = new Date(startDate);
        let endDate = obj.offlineTime.split(" ");
        endDate = endDate[0];
        endDate = new Date(endDate);
        this.dateRangeVal = [];
        this.dateRangeVal.push(startDate);
        this.dateRangeVal.push(endDate);
        this.getReTableData(1);
        this.panelIshide = false;
        this.btnGroupActive = [true, false];
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      let device = this.$parent.options[this.$parent.optionsVal];
      vm.$http.all([
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'RemoteOperationHistory'
            }
          }),
          vm.$http.get('/wyfx/sdFile/getParameterLog', {
            params: {
              deviceId: device.id,
              pageNo: 1,
              pageSize: vm.rePageSize,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime()
            }
          }),
          vm.$http.get('/wyfx/device/getOnlineAndOfflineTime', {
            params: {
              deviceId: device.id,
              pageNo: 1,
              pageSize: vm.tiPageSize
            }
          })
        ])
        .then(vm.$http.spread(function(language, reData, tiData) {
          // 獲取文本信息
          vm.language = language.data.data;
          // 操作记录
          if (reData.data.code == 200) {
            vm.renderReTable(reData);
          }
          // 连接时间
          if (tiData.data.code == 200) {
            vm.renderTiTable(tiData);
          }
          loading.close();
        }))
        .catch(error => {
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>

<style scoped>
  .panel-toolBar {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  .panel-toolBar-cs {
    margin: 0 10px;
  }
  .table {
    width: 100%;
    height: calc(100% - 82px);
  }
  .tiTable {
    width: 100%;
    height: calc(100% - 42px);
  }
  .hide {
    display: none;
  }
</style>



