<template>
  <div class="outer">
    <el-row class="fullHeight">
      <el-col :span="24" class="fullHeight">
        <div class="panel">
          <div class="panel-title">
            {{language.panelTitle}}
          </div>
          <div class="panel-content">
            <el-form ref="form" :inline="true" :model="form" label-width="100px" class="form">
              <div class="form-title">{{language.formTitle1}}</div>
              <el-form-item :label="language.formUrl1">
                <el-input v-model="form.url1" :placeholder="language.urlPH"></el-input>
              </el-form-item>
              <el-form-item :label="language.formUrl2">
                <el-input v-model="form.url2" :placeholder="language.urlPH"></el-input>
              </el-form-item>
              <el-form-item :label="language.formUrl3">
                <el-input v-model="form.url3" :placeholder="language.urlPH"></el-input>
              </el-form-item>
            </el-form>
            <el-form ref="form" :inline="true" :model="form" label-width="100px" class="form">
              <div class="form-title">{{language.formTitle2}}</div>
              <div class="el-form-item">
                <label class="el-form-item__label" style="width: 100px;font-weight: bold">{{language.formMbNetwork}}</label>
                <span class="el-form-item__label" style="color: #1caf9a">{{signalType}}</span>
                <img :src="require('../assets/img/low-signal-white.png')" alt="" class="img-signal" v-if="signal == 0">
                <img :src="require('../assets/img/mid-signal-white.png')" alt="" class="img-signal" v-else-if="signal == 1">
                <img :src="require('../assets/img/strong-signal-white.png')" alt="" class="img-signal" v-else-if="signal == 2">
              </div>
            </el-form>
            <el-form ref="form" :model="form" label-width="170px" class="form">
              <div class="form-title">{{language.formTitle3}}</div>
              <el-form-item :label="language.formOlTime">
                <el-slider :max="120" v-model="form.onlineWaitTime" show-input>
                </el-slider>
              </el-form-item>
              <el-form-item :label="language.formOffTime">
                <el-slider :max="120" v-model="form.dormantTime" show-input>
                </el-slider>
              </el-form-item>
            </el-form>
            <el-form ref="form" :model="form" label-width="170px" class="form">
              <div class="form-title">{{language.formTitle5}}
                <el-switch v-model="form.scanStatus" on-text="" off-text="" on-value="1" off-value="0" style="margin: -3px 0 0 10px">
                </el-switch>
              </div>
              <div class="el-form-item">
                <label class="el-form-item__label">{{language.formDontScanTxt}}</label>
                <el-input type="textarea" autosize v-model="form.path">
                </el-input>
              </div>
            </el-form>
            <el-form ref="form" :inline="true" :model="form" label-width="100px" class="form">
              <div class="form-title">{{language.formTitle4}}</div>
              <div class="el-form-item">
                <label class="el-form-item__label" style="width: 140px;">{{language.formAppVer}}</label>
                <span class="el-form-item__label" style="color: #1caf9a">{{form.apkVersion}}</span>
              </div>
              <div class="el-form-item">
                <label class="el-form-item__label" style="width: 140px;">{{language.formAppUpTime}}</label>
                <span class="el-form-item__label" style="color: #1caf9a">{{form.apkVersion}}</span>
              </div>
              <el-upload :action="this.$rootPath + '/wyfx/device/uploadApk'" :on-success="uploadSuccess" :file-list="fileList" class="upload" :data="{deviceId:this.$parent.options[this.$parent.optionsVal].id}" ref="upload">
                <el-button size="small" type="primary">{{language.formAppUpTxt}}</el-button>
              </el-upload>
              <div>
                <label class="el-form-item__label" style="width: 113px;">{{language.formAppUpVer}}</label>
                <span class="el-form-item__label" style="color: #1caf9a">{{form.stayApkVersion}}</span>
              </div>
              <div>
                <label class="el-form-item__label" style="width: 86px;">{{language.formAppUpType}}</label>
                <span class="el-form-item__label" style="color: #1caf9a">{{language.uploadTypeTxt}}</span>
              </div>
              <div class="form-sub">
                <el-button type="primary" @click="subConfig">{{language.formSubBtn}}</el-button>
              </div>
            </el-form>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        language: {},
        form: {},
        fileList: [],
        signal: 0,
        signalType: ''
      }
    },
    methods: {
      uploadSuccess(response, file, fileList) {
        if (response.code == 200) {
          this.form.stayApkVersion = response.msg;
        } else if (response.code == 300) {
          this.$message({
            showClose: true,
            message: this.language.fileErr,
            type: 'error'
          });
          for (var i = 0; i < fileList.length; i++) {
            if (fileList[i].uid == file.uid) {
              fileList.splice(i, 1);
              break;
            }
          }
        } else {
          this.$message({
            showClose: true,
            message: this.language.cantUpload,
            type: 'warning'
          });
          for (var i = 0; i < fileList.length; i++) {
            if (fileList[i].uid == file.uid) {
              fileList.splice(i, 1);
              break;
            }
          }
        }
      },
      // 提交表单
      subConfig() {
        const vm = this;
        vm.$httpi.get('/wyfx/device/updateDeviceConfiguration', {
            params: {
              configuration: vm.form
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.$message({
                showClose: true,
                message: vm.language.subSuc,
                type: 'success'
              });
            } else {
              vm.$message({
                showClose: true,
                message: vm.language.subFaild,
                type: 'error'
              });
            }
          });
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      let device = this.$parent.options[this.$parent.optionsVal];
      vm.$http.all([
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'DiskRunConfig'
            }
          }),
          vm.$http.get('/wyfx/device/getDeviceConfiguration', {
            params: {
              deviceId: device.id
            }
          })
        ])
        .then(vm.$http.spread(function(language, config) {
          // 獲取文本信息
          vm.language = language.data.data;
          // 配置
          if (config.data.code == 200) {
            vm.form = config.data.data;
            if (vm.form.scanStatus === 1) {
              vm.form.scanStatus = "1";
            } else {
              vm.form.scanStatus = "0";
            }
            if (config.data.map.code == 200) {
              if (config.data.map.signal < -80) {
                vm.signal = 0;
              } else if (config.data.map.signal > -50) {
                vm.signal = 2;
              } else {
                vm.signal = 1;
              }
              vm.signalType = config.data.map.signalType;
            } else {
              vm.signal = -1;
              vm.signalType = vm.language.signalTypeNull;
            }
          }
          loading.close();
        }))
        .catch(error => {
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>
<style scoped>
  .panel-content {
    overflow: auto;
  }
  .form {
    width: 1000px;
    margin: 0 auto;
  }
  .form-title {
    font-size: 16px;
    color: #1caf9a;
    font-weight: bold;
    margin: 20px 0;
  }
  .el-form-item {
    margin-bottom: 10px;
  }
  .img-signal {
    margin-bottom: -2px
  }
  .upload {
    margin-bottom: 10px;
    max-height: 123px;
    overflow: auto;
  }
  .form-sub {
    width: 100%;
    text-align: center;
    padding: 30px 0;
  }
</style>



