<template>
  <div class="outer">
    <div class="toolBar clearfix">
      <refresh @refreshData="refreshData"></refresh>
      <div class="fl">
        <el-button class="toolBar-btn" type="primary" @click="commandDownload">
          <i class="iconfont icon-icon-31 btn-icon"></i>{{language.download}}</el-button>
        <el-button class="toolBar-btn" type="primary" @click="openUpload">
          <i class="iconfont icon-icon-51 btn-icon"></i>{{language.upload}}</el-button>
        <el-button class="toolBar-btn" type="primary" @click="commandDel">
          <i class="iconfont icon-icon-81 btn-icon"></i>{{language.del}}</el-button>
        <el-button class="toolBar-btn" type="primary" @click="commandScan" v-if="isOl == true">{{language.scan}}</el-button>
      </div>
    </div>
    <div class="panel" :class="{hide: panelIshide[0]}">
      <div class="panel-title">
        {{language.panelHistory}}
      </div>
      <div class="panel-info">
        {{language.lastSyn}}{{synTime}}
      </div>
      <div class="panel-content">
        <el-breadcrumb separator="/" class="crumb">
          <el-breadcrumb-item v-for="(item2, index) in crumbData" :key="item2">
            <span @click="crumbClick(index)">{{item2}}</span>
          </el-breadcrumb-item>
        </el-breadcrumb>
        <el-table :data="tableData" class="file-table" style="width: 100%;" height="100%" :show-header="false" @select="selectRow">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column>
            <template scope="scope">
                <div>
                  <i class="iconfont icon-icon-9 table-icon" v-if="scope.row.type == 0"></i>
                  <i class="iconfont icon-icon-10 table-icon" v-else></i>
                  <a class="file-table-a" @click="handleEnterFolder(scope.row)">{{scope.row.filename}}</a>
                </div>
</template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="panel" :class="{hide: panelIshide[1]}">
      <div class="panel-title">
        {{language.panelRealTime}}
        <img :src="require('../assets/img/low-signal.png')" alt="" class="img-signal" v-if="signal == 0">
        <img :src="require('../assets/img/mid-signal.png')" alt="" class="img-signal" v-else-if="signal == 1">
        <img :src="require('../assets/img/strong-signal.png')" alt="" class="img-signal" v-else-if="signal == 2">
        <img :src="require('../assets/img/battery.png')" alt="" class="img-battery">
        <span class="txt-battery">{{energy}}</span>
      </div>
      <div class="panel-toolBar">
        {{language.remote}}
        <el-switch v-model="switchVal" on-text="" off-text="" on-value="1" off-value="0" @change="switchChange">
        </el-switch>
        <el-button size="small" type="primary" style="margin-left: 20px;" @click="powerOff">{{language.closeDevice}}</el-button>
      </div>
      <div class="panel-content">
        <el-breadcrumb separator="/" class="crumb">
          <el-breadcrumb-item v-for="(item, index) in crumbData" :key="item">
            <span @click="crumbClick(index)">{{item}}</span>
          </el-breadcrumb-item>
        </el-breadcrumb>
        <el-table :data="tableData" class="file-table" style="width: 100%;" height="100%" :show-header="false" @select="selectRow">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column>
<template scope="scope">
  <div>
    <i class="iconfont icon-icon-9 table-icon" v-if="scope.row.type == 0"></i>
    <i class="iconfont icon-icon-10 table-icon" v-else></i>
    <a class="file-table-a" @click="handleEnterFolder(scope.row)">{{scope.row.filename}}</a>
  </div>
</template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="panel">
      <el-table :data="recordTableData" style="width: 100%;" height="100%" class="record-table">
        <el-table-column prop="pDate" :label="language.tabPDate"></el-table-column>
        <el-table-column prop="eDate" :label="language.tabEDate"></el-table-column>
        <el-table-column prop="path" :label="language.tabPath"></el-table-column>
        <el-table-column prop="filename" :label="language.tabFileName"></el-table-column>
        <el-table-column :label="language.tabDownCommand">
<template scope="scope">
  <span v-if="scope.row.downcommand == 110">{{language.upload}}</span>
  <span v-if="scope.row.downcommand == 101">{{language.download}}</span>
  <span v-if="scope.row.downcommand == 111">{{language.del}}</span>
</template>
        </el-table-column>
        <el-table-column :label="language.tabRunType">
<template scope="scope">
  <span v-if="scope.row.runType == 0">{{language.toBeExecuted}}</span>
  <span v-if="scope.row.runType == 1">{{language.completed}}</span>
</template>
        </el-table-column>
        <el-table-column :label="language.tabDataType">
<template scope="scope">
  <span v-if="scope.row.dataType == 0">{{language.toBeExecuted}}</span>
  <span v-if="scope.row.dataType == 1">{{language.completed}}</span>
</template>
        </el-table-column>
        <el-table-column :label="language.tabStatus">
<template scope="scope">
  <span v-if="scope.row.dataType == 0">{{language.executing}}</span>
  <span v-if="scope.row.dataType == 1">{{language.completed}}</span>
</template>
        </el-table-column>
        <el-table-column :label="language.tabAct">
<template scope="scope">
  <el-button size="small" type="text" v-if="scope.row.dataType == 1 && scope.row.downcommand == 101" @click="downLoad(scope.row)">
    {{language.tabDownload}}</el-button>
</template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[20, 50, 100, 200, 400]"
          :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import refresh from '../components/Refresh.vue'
  export default {
    components: {
      refresh
    },
    data() {
      return {
        language: {},
        panelIshide: [false, true],
        switchVal: 0,
        tableData: [],
        crumbData: [],
        selection: [],
        energy: '',
        signal: 0,
        synTime: '',
        recordTableData: [],
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 20, // 每页显示条目数
        isOl: false
      }
    },
    methods: {
      // 伪刷新
      refreshData() {
        this.$parent.handleOptionChange();
      },
      // 面包屑导航点击
      crumbClick(index) {
        const vm = this;
        let arr = this.crumbData;
        let path = '';
        for (var i in arr) {
          if (i <= index) {
            path += '/' + arr[i];
          }
        }
        // 请求数据重新渲染文件
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/sdFile/getFileAndFolder', {
            params: {
              deviceId: device.id,
              path: path
            }
          })
          .then(function(response) {
            vm.renderFiles(response);
          });
      },
      //渲染硬盘文件
      renderFiles(response) {
        if (response.data.map.status == -1) {
          this.panelIshide = [false, true];
          this.synTime = response.data.map.time;
          this.isOl = false;
        } else {
          this.panelIshide = [true, false];
          this.isOl = true;
          this.energy = response.data.map.energy;
          this.switchVal = response.data.map.remoteStatus.toString();
          if (response.data.map.signal < -80) {
            this.signal = 0;
          } else if (response.data.map.signal > -50) {
            this.signal = 2;
          } else {
            this.signal = 1;
          }
        }
        if (response.data.code == 200) {
          this.tableData = response.data.data;
        } else {
          this.tableData = [];
          this.$message({
            showClose: true,
            message: this.language.nullTxt,
            type: 'warning'
          });
        }
        let str = response.data.map.path;
        this.crumbData = str.split("/");
        this.crumbData.splice(0, 1);
      },
      // 打开文件夹
      handleEnterFolder(row) {
        if (row.type == 1) {
          return false;
        }
        const vm = this;
        let path = row.path + '/' + row.filename;
        let deviceId = row.deviceId;
        vm.$httpi.get('/wyfx/sdFile/getFileAndFolder', {
            params: {
              deviceId: deviceId,
              path: path
            }
          })
          .then(function(response) {
            vm.renderFiles(response);
          });
      },
      // 表格行选择
      selectRow(selection) {
        this.selection = selection;
      },
      // 获取命令记录
      getRecordTableData(pageNo) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        vm.$httpi.get('/wyfx/sdFile/getParameterLog', {
            params: {
              pageNo: pageNo,
              pageSize: vm.pageSize,
              deviceId: device.id
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.renderRecordTable(response);
            }
          });
      },
      // 渲染命令记录
      renderRecordTable(response) {
        this.recordTableData = response.data.data.content;
        this.totalElements = response.data.data.totalElements;
        this.currentPage = response.data.data.pageNo;
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.getRecordTableData(1);
      },
      handleCurrentChange(currentPage) {
        this.getRecordTableData(currentPage);
      },
      // 下载文件
      downLoad(obj) {
        const vm = this;
        this.$httpi.get('/wyfx/sdFile/isExistFile', {
            params: {
              deviceId: obj.deviceId,
              path: obj.path,
              filename: obj.filename
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              window.open(vm.$rootPath + '/wyfx/sdFile/download?deviceId=' + obj.deviceId + '&path=' + obj.path +
                "&filename=" + obj.filename);
            } else {
              vm.$message({
                showClose: true,
                message: vm.language.files404,
                type: 'error'
              });
            }
          });
      },
      // 下载指令
      commandDownload() {
        let path = '';
        let arr = this.selection;
        if (arr.length < 1) {
          this.$message({
            showClose: true,
            message: this.language.commandMes,
            type: 'warning'
          });
        } else {
          for (let i in arr) {
            if (arr[i].type == 0) {
              this.$message({
                showClose: true,
                message: this.language.noFolder,
                type: 'warning'
              });
              return false;
            } else {
              path += arr[i].path + '/' + arr[i].filename + '#';
            }
          }
        }
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        this.$httpi.get('/wyfx/sdFile/downloadFile', {
            params: {
              deviceId: device.id,
              path: path
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.$message({
                showClose: true,
                message: vm.language.commandSuc,
                type: 'success'
              });
            }
          });
      },
      // 删除指令
      commandDel() {
        let path = '';
        let arr = this.selection;
        if (arr.length < 1) {
          this.$message({
            showClose: true,
            message: this.language.commandMes,
            type: 'warning'
          });
        } else {
          for (let i in arr) {
            path += arr[i].path + '/' + arr[i].filename + '#';
          }
          const vm = this;
          let device = this.$parent.options[this.$parent.optionsVal];
          this.$httpi.get('/wyfx/sdFile/deleteFile', {
              params: {
                deviceId: device.id,
                path: path
              }
            })
            .then(function(response) {
              if (response.data.code == 200) {
                vm.$message({
                  showClose: true,
                  message: vm.language.commandSuc,
                  type: 'success'
                });
              }
            });
        }
      },
      // 扫描指令
      commandScan() {
        let path = '';
        let arr = [];
        for (let i in this.selection) {
          if (this.selection[i].type == 0) {
            arr.push(this.selection[i]);
          }
        }
        if (arr.length < 1) {
          this.$message({
            showClose: true,
            message: this.language.commandMes2,
            type: 'warning'
          });
        } else {
          for (let j in arr) {
            path += arr[j].path + '/' + arr[j].filename + '#';
          }
          const vm = this;
          let device = this.$parent.options[this.$parent.optionsVal];
          this.$httpi.get('/wyfx/sdFile/findFile', {
              params: {
                deviceId: device.id,
                path: path
              }
            })
            .then(function(response) {
              if (response.data.code == 200) {
                vm.$message({
                  showClose: true,
                  message: vm.language.commandSuc,
                  type: 'success'
                });
              }
            });
        }
      },
      // 打开上传弹窗
      openUpload() {
        let arr = this.crumbData;
        let path = '';
        for (var i in arr) {
          path += '/' + arr[i];
        }
        this.$parent.upLoadData = {
          deviceId: this.$parent.options[this.$parent.optionsVal].id,
          path: path
        }
        this.$parent.uploadDialogVisible = true;
      },
      // 远程操作指令
      switchChange(val) {
        const vm = this;
        let device = this.$parent.options[this.$parent.optionsVal];
        this.$httpi.get('/wyfx/sdFile/isRemote', {
            params: {
              deviceId: device.id,
              status: val
            }
          })
          .then(function(response) {
            if (response.data.code == 200) {
              vm.$message({
                showClose: true,
                message: vm.language.actSuc,
                type: 'success'
              });
            } else {
              vm.$message({
                showClose: true,
                message: vm.language.actErr,
                type: 'error'
              });
              if (val == 1) {
                vm.switchVal = 0;
              } else {
                vm.switchVal = 1;
              }
            }
          });
      },
      // 关机指令
      powerOff() {
        this.$confirm(this.language.confirmTxt, this.language.confirmTitle, {
          confirmButtonText: this.language.confirmYes,
          cancelButtonText: this.language.confirmNo,
          type: 'warning'
        }).then(() => {
          const vm = this;
          let device = this.$parent.options[this.$parent.optionsVal];
          this.$httpi.get('/wyfx/sdFile/powerOff', {
              params: {
                deviceId: device.id
              }
            })
            .then(function(response) {
              if (response.data.code == 200) {
                vm.$message({
                  showClose: true,
                  message: vm.language.actSuc,
                  type: 'success',
                  onClose: function() {
                    vm.$router.replace({
                      name: 'RouterTransit',
                      params: {
                        name: vm.$route.name
                      }
                    });
                  }
                });
              } else {
                vm.$message({
                  showClose: true,
                  message: vm.language.actErr,
                  type: 'error'
                });
              }
            });
        })
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      let device = this.$parent.options[this.$parent.optionsVal];
      vm.$http.all([
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'DiskManage'
            }
          }),
          vm.$http.get('/wyfx/sdFile/getFileAndFolder', {
            params: {
              deviceId: device.id,
              path: ''
            }
          }),
          vm.$http.get('/wyfx/sdFile/getParameterLog', {
            params: {
              deviceId: device.id,
              pageNo: 1,
              pageSize: vm.pageSize,
            }
          })
        ])
        .then(vm.$http.spread(function(language, file, record) {
          // 獲取文本信息
          vm.language = language.data.data;
          // 获取硬盘文件
          if (file.data.code == 200) {
            vm.renderFiles(file);
          }
          // 文件操作日志
          if (record.data.code == 200) {
            vm.renderRecordTable(record);
          }
          loading.close();
        }))
        .catch(error => {
          console.log(error);
          vm.$router.push({
            name: 'Error'
          })
        });
    }
  }
</script>

<style scoped>
  .btn-icon {
    font-size: 14px;
    margin-right: 5px;
  }
  .table-icon {
    font-size: 22px;
  }
  .refresh-img {
    padding-top: 17px;
    margin-right: 20px;
    cursor: pointer;
  }
  .panel {
    margin-top: 5px;
    height: calc((100% - 65px)/2);
  }
  .panel-title {
    position: relative;
  }
  .hide {
    display: none;
  }
  .panel-info {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    color: #aeb3b7;
    padding-left: 20px;
  }
  .panel-content {
    height: calc(100% - 85px);
  }
  .panel-toolBar {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    padding-left: 20px;
  }
  .crumb {
    height: 15px;
    line-height: 15px;
    padding: 5px;
    margin-left: 15px;
  }
  .file-table {
    width: 100%;
    height: calc(100% - 25px);
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
  }
  .file-table-a {
    cursor: pointer;
  }
  .file-table-a:hover {
    text-decoration: underline;
  }
  .img-signal {
    position: absolute;
    top: 13px;
    right: 100px;
  }
  .img-battery {
    position: absolute;
    top: 14px;
    right: 45px;
  }
  .txt-battery {
    position: absolute;
    top: 0;
    right: 10px;
    color: #b9e5df;
    font-size: 14px;
  }
  .record-table {
    width: 100%;
    height: calc(100% - 42px);
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
  }
</style>



